@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700;800;900&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);

@use "./variables" as *;*{padding:0;margin:0;box-sizing:border-box}html{font-size:100%}@media only screen and (max-width: 1024px){html{font-size:80%}}@media only screen and (max-width: 600px){html{font-size:60%}}body{font-family:"Montserrat",sans-serif;font-weight:400;line-height:1.5}@media only screen and (max-width: 600px){body{margin-bottom:3rem}}a{text-decoration:none;color:unset}button,input{outline:0}ul{list-style-type:none}img{max-width:100%}.App{background:#000;width:100%;height:100vh}


@use "./variables" as *;.header{width:100%;color:#fff;font-size:14px;position:fixed;top:0;z-index:999;background:linear-gradient(to top, transparent 0%, rgba(0,0,0,0.3) 50%);transition:all 0.3s ease}.header.scrolled{-webkit-backdrop-filter:blur(15px);backdrop-filter:blur(15px)}.header .container{display:flex;align-items:center;justify-content:space-between;padding:0 50px;height:70px;font-size:18px}.header .container .left{display:flex;align-items:center}.header .container .left img{height:60px;margin-right:40px;margin-top:-10px}.header .container .left a{margin-right:20px}.header .container .left a:hover{color:red}.header .container .right{display:flex;align-items:center}.header .container .right .userName{color:red}.header .container .right button{width:70px;margin:0 10px;padding:5px;border:1px solid red;background:transparent;color:#fff;cursor:pointer}.header .container .right button:active{-webkit-transform:translateY(1px);transform:translateY(1px)}@media only screen and (max-width: 600px){.header{position:static;margin-bottom:50px}.header .container{flex-direction:column}.header .container .left{flex-direction:column}}

@use "./variables" as *;.banner{height:100vh;color:#fff;display:flex;align-items:center;justify-content:center;flex-direction:column;background-position:center;background-repeat:no-repeat;background-size:cover}.banner .welcome{margin-bottom:50px}.banner .welcome span{font-size:38px;color:#E50914}.banner .textContainer{width:80%}.banner .textContainer h1{font-size:70px}.banner .textContainer h4{font-size:24px;margin-bottom:10px}.banner .textContainer span{background:#fff;color:#000;padding:3px;border-radius:5px;font-weight:600;font-size:18px}.banner .textContainer p{margin-top:15px}.banner .textContainer button{width:200px;padding:10px;margin-right:20px;margin-top:30px;border:none;background:#E50914;color:#fff;font-weight:600;font-size:18px;font-family:'Montserrat', sans-serif;text-transform:uppercase;cursor:pointer;border-radius:5px}.banner .textContainer button:hover{background:#f8434c;-webkit-transform:scale(1.1);transform:scale(1.1);box-shadow:0px 0px 11px 8px rgba(229,9,20,0.2)}@media only screen and (max-width: 600px){.banner .welcome{margin-bottom:30px;display:flex;flex-direction:column}.banner .welcome span{font-size:38px;color:#E50914}.banner .textContainer h1{font-size:35px}}@media (orientation: landscape) and (max-width: 667px){.banner{height:100vh}.banner .welcome{margin-bottom:30px;display:flex;flex-direction:column}.banner .welcome span{font-size:38px;color:#E50914}.banner .textContainer h1{font-size:35px}.banner .textContainer button{font-size:14px;width:150px;font-weight:400;padding:5px;margin-bottom:15px}}.swiper-pagination-bullet{background:white !important}.swiper-pagination-bullet-active{background:red !important}

@use "./variables" as *;.card_container{background:url(data:image/gif;base64,);width:100%;height:100%}.card_container .card_wrapper{display:flex;justify-content:space-between;width:55%;flex-wrap:wrap}.card_container .card_wrapper .card{position:relative;width:300px;height:420px;margin:0 10px;margin-top:100px;border-radius:5px;display:flex;flex-direction:column;overflow:hidden;flex-wrap:nowrap}.card_container .card_wrapper .card:hover .card_overlay{visibility:visible}.card_container .card_wrapper .card .card_overlay{position:absolute;width:100%;height:100%;background-color:rgba(0,0,0,0.7);display:flex;align-items:center;justify-content:center;cursor:pointer;transition:all 0.3 ease;visibility:hidden}.card_container .card_wrapper .card .card_overlay svg{font-size:72px;color:#808080}.card_container .card_wrapper .card:hover{background-color:rgba(0,0,0,0.3)}.card_container .card_wrapper .card .card_top{display:flex;width:100%;height:100%;align-items:start;background-position:center;background-repeat:no-repeat;background-size:cover}.card_container .card_wrapper .card .card_top .card_title{display:flex;justify-content:space-between;align-items:center;width:100%;padding:5px;color:#fff}.card_container .card_wrapper .card .card_top .card_title .title{flex:8 1}.card_container .card_wrapper .card .card_top .card_title svg{font-size:32px;cursor:pointer;flex:1 1;color:#000}.card_container .card_wrapper .card .card_top .card_title svg:hover{-webkit-transform:scale(1.2);transform:scale(1.2)}.card_container .card_wrapper .card .card_bottom{background-color:#fff;border-radius:0 0 5px 5px;background-color:#808080}@media only screen and (max-width: 600px){.card_container .card_wrapper{width:85%}}

@use "./variables" as *;.movie_modal{width:730px;height:500px;background-color:#fff;margin:auto;position:absolute;left:50%;top:0;-webkit-transform:translate(-50%, 50%);transform:translate(-50%, 50%);z-index:99}.movie_modal .movie_modal_top{display:flex;align-items:flex-end;background-color:#b3b3b3;padding:10px;position:relative}.movie_modal .movie_modal_top .close_btn{position:absolute;top:3px;right:5px;font-size:35px;cursor:pointer}.movie_modal .movie_modal_top .poster{margin-right:20px;height:200px}.movie_modal .movie_modal_top .poster img{width:140px;height:100%;object-fit:cover;border-radius:3px}.movie_modal .movie_modal_top .watch_btn{margin-right:20px}.movie_modal .movie_modal_top .watch_btn button{width:150px;height:45px;font-size:18px;display:flex;align-items:center;justify-content:center;background-color:#fc3030;border:none;color:#fff;font-weight:500;cursor:pointer;border-radius:5px}.movie_modal .movie_modal_top .watch_btn button:hover{background-color:#dc0303}.movie_modal .movie_modal_top .watch_btn button svg{font-size:24px}.movie_modal .movie_modal_top .rate_btn{display:flex;flex-direction:column;align-items:center;margin-right:20px;cursor:pointer}.movie_modal .movie_modal_top .rate_btn svg{font-size:24px}.movie_modal .movie_modal_top .rate_btn span{font-size:12px}.movie_modal .movie_modal_top .rate_btn.like:hover{color:green}.movie_modal .movie_modal_top .rate_btn.dislike:hover{color:red}.movie_modal .movie_modal_bottom{padding:10px;text-align:left}.movie_modal .movie_modal_bottom .video_info{margin-bottom:20px}.movie_modal .movie_modal_bottom .video_info .info_year{font-weight:700;margin-right:10px}.movie_modal .movie_modal_bottom .video_info .info_genre{font-weight:500;margin-right:20px}.movie_modal .movie_modal_bottom .video_info .info_country{font-weight:700}.movie_modal .movie_modal_bottom .video-extra{color:#808080;margin-bottom:30px}.movie_modal .movie_modal_bottom .video-extra .video_quality{font-size:12px;border:1px solid #808080;border-radius:3px;padding:3px;margin-right:20px}.movie_modal .movie_modal_bottom .video-extra .video_duration{font-weight:600}.movie_modal .movie_modal_bottom .video_description{margin-bottom:30px}.movie_modal .movie_modal_bottom .video_cast{font-weight:600}@media only screen and (max-width: 600px){.movie_modal{width:90%;top:-30%}.movie_modal .movie_modal_top{flex-wrap:wrap}.movie_modal .movie_modal_top .poster{margin-bottom:5px}}

