@use "./variables" as *;.login{width:100vw;height:100vh;background-image:url(/testMovieApp/static/media/footer-bg.67e95f05.jpg);background-size:cover;position:relative}.login .container{width:100%;height:100%;position:absolute;top:0;left:0;display:flex;align-items:center;justify-content:center;flex-direction:column;color:#fff}.login .container form{width:365px;height:350px;padding:30px;border-radius:5px;background-color:#000;display:flex;flex-direction:column;justify-content:space-around;box-shadow:0px 0px 11px 12px rgba(221,0,0,0.4);position:relative}.login .container form input{height:40px;border-radius:5px;background-color:lightgray;padding-left:10px;margin:5px 0}.login .container form .loginBtn{height:40px;border-radius:5px;background:#E50914;border:none;font-size:18px;font-weight:500;cursor:pointer;margin-top:10px}.login .container form .loginBtn:active{-webkit-transform:translateY(1px);transform:translateY(1px)}.login .container form span{color:lightgray;margin:10px 0}.login .container form span b{color:#fff}.login .container form span b:hover{color:#E50914}.login .container form .goBack{display:flex;align-items:center;width:30%}.login .container form .goBack svg{margin-right:5px}.login .container .errMessage{margin-top:5px;position:absolute;left:8%;top:2%}@media only screen and (max-width: 600px){.login .container{width:100%}.login .container form{width:320px}}

